import React from 'react';
import { BrowserRouter, Routes, Route } from "react-router-dom";
import './App.css';
import Login from './layouts/login';
import Dash from './layouts/dash';
import Inbox from './layouts/inbox';
import Departments from './layouts/department';
import RecoveryPass from './layouts/recoverypass';
import Register from './layouts/newuser';


function App() {
  return (
    <div className="App">
      <Login></Login>
    <BrowserRouter>
      <Routes>
        
        <Route path="/register" element={<Register></Register>}></Route>
        <Route path="/PasswordRecovery" element={<RecoveryPass></RecoveryPass>}></Route>
        <Route path="/dash" element={<Dash></Dash>}></Route>
      </Routes>
    </BrowserRouter>
    </div>
  );
}
export default App;
