import React, { useState } from "react";
import Input from '../component/input/input'
import SelectUser from "../component/selectuser/selectuser";
import logo from "../assests/Mihan.png"
import { Link } from "react-router-dom";
import "./recoverypass.css"
const RecoveryPass=()=>{
    const [email, setEmail] = useState("");
    const [codeNumber, setCodeNumber] = useState("");
    const [phonenumber, setPhonenumber] = useState("");

    const Input1=<Input
    type={"mail"}
    value={email}
    inputChenge={(e) => setEmail(e.target.value)}
    childeren={" "}
/> ;
const Input2=<Input
     type={"number"}
     value={phonenumber}
     inputChenge={(e) => setPhonenumber(e.target.value)}
     childeren={"  "}
 />
 const Input3=<Input
     type={"number"}
     value={codeNumber}
     inputChenge={(e) => setCodeNumber(e.target.value)}
     childeren={" "}
 />

    const handleSubmit = (e) => {
        e.preventDefault();
        if(email='rez@gmail.com'){}
      };
    return(
    <React.Fragment>
    <div className="toolbar">
      <div className="logo">
      <img src={logo} width="60" height="35" ></img>
      </div>
      <div className="noting">
      </div>
      <div className="homepage">  
      </div>
    </div>

    <div className="boxrecpass">
      <span className="text-centerrecpass">
        <h1  >
        <div className="boxlabelrecpass">  </div>
        </h1>
        <form onSubmit={handleSubmit}>
            {Input1}
            {Input2}
            {Input3}
          <button  type="submit" className="btnrecpass"  Link="./2">  </button>
       </form>
       </span>
    </div>
  </React.Fragment>
    );
}
export default RecoveryPass