import React, { useState } from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Icon from "../component/icons/icons";
import "./dash.css"
import logo from "../assests/Mihan.png"
import reqlogo from "../assests/request.jpg"
import Anlogo from "../assests/analyz.jpg"
import Req from "../component/req/req";
import Inbox from "./inbox";
import Departments from "./department";

const Dash=(props)=>{
    const [element,setElement]=useState(<div></div>)
    const onClickHandeler1=()=>{
      setElement(<div className="disp"><Req></Req></div>)

    }
    const onClickHandeler2=()=>{
      setElement(<div className="disp"><Inbox></Inbox></div>)

    }
    const onClickHandeler3=()=>{
      setElement(<div className="disp"><Departments></Departments></div>)

    }

    
    


    return(
        <React.Fragment>
        <div className="toolbar">
          <div className="logo">
          <img src={logo} width="60" height="35" ></img>
          </div>
          <div className="noting">
          </div>
          <div className="homepage"></div>
        </div>
    
        <div className="menubar">
          <div className="icons" >
            <Icon LOgo={reqlogo} label={"  "} Clicked={onClickHandeler2}></Icon>
            <Icon LOgo={Anlogo}  label={"   "} Clicked={onClickHandeler1}></Icon>
            <Icon LOgo={reqlogo} label={" "}  Clicked={onClickHandeler1}></Icon>
            <Icon LOgo={Anlogo}  label={"  "}  Clicked={onClickHandeler3}></Icon>
            <Icon LOgo={reqlogo} label={"   "} Clicked={onClickHandeler1}></Icon>
            <Icon LOgo={reqlogo} label={"  "}  Clicked={onClickHandeler1}></Icon>
          </div>
        </div>
          {element}

      </React.Fragment>

    );
}
export default Dash
