import React, { useState } from "react";
import "./select.css"
const Optionsvalue=[{id:0,name:" ",position:" "}
  ,{id:1,name:"  ",position:" "}
  ,{id:2,name:" ",position:""}
  ,{id:3,name:"  ",position:""}
  ,{id:4,name:" ",position:" "}
  ,{id:5,name:" ",position:"   "}
  ,{id:6,name:"  ",position:" "}
  ,{id:7,name:"  ",position:"   "}
  ,{id:8,name:"  ",position:" "}
]
const Addreciver=()=>{

}

const Select = (props) => {
  const aaa=`  `;
  const [opttionValueChaenge,setOpttionValueChaenge]=useState(Optionsvalue);
  const [serchBarValue,setSerchBarValue]=useState('');
  const [selectedOptions,setSelectedOptions]=useState([])
  const [selectStyle, setSelectStyle] = useState({display:"none"});

  const serchBarChangeHandeler=(e)=>{
    setSerchBarValue(e.target.value);
    const ValueUp=e.target.value.toUpperCase();
    /*const oldOpttionValueChaenge=opttionValueChaenge;*/
    setOpttionValueChaenge(Optionsvalue.filter((item)=>{
      const itemsUp=item.name.toUpperCase();
      return itemsUp.indexOf(ValueUp)>-1;
    }))
  };
  
  const selectHandeler=(e,id)=>{
    const s=selectedOptions;
    const indx = s.indexOf(id);
    if (indx > -1) { 
      s.splice(indx, 1);
      e.target.style.backgroundColor ='white';
    }
    else{
      s.push(id);
      e.target.style.backgroundColor ='green';
    }
    setSelectedOptions(s);
  }
  const okClicHandeler=()=>{
    setSelectStyle({display:'none'});

  }
  const Addreciver=()=>{
    setSelectStyle({display:'block'});
  }
  const SelectedUsers=selectedOptions.length==0?<span className="slclabel">"  "</span>:
  <div className="slectedlist" > {Optionsvalue.filter((item)=>{ 
    const itemsUp=item.id;
    return selectedOptions.includes(itemsUp);
  }).map((itm)=>{return(<div className="selectedOptionsList">{itm.name}</div>)})
    }</div>

  return (
    <React.Fragment>
    <div className="slc" onClick={Addreciver}>
      {SelectedUsers}
      
    </div>
    <div className="slcrecivers" style={selectStyle}>
      <input className="searvhbar" placeholder="" value={serchBarValue} onChange={serchBarChangeHandeler}>

      </input>
      <div className="optreciver">
        {opttionValueChaenge.map((opt,index) => (
          <div className="recivers"  onClick={(e)=>selectHandeler(e,opt.id)}>
            {opt.id}  {opt.name} {aaa}  ({opt.position})
        </div>
          ))}
        </div>
        <div className="slcbtn">
          <button className="sbtn" onClick={okClicHandeler}> ok</button>
        </div>
    </div>
    </React.Fragment>
  );
};
export default Select;
