import React ,{useState }from "react";
import './input.css'

const Input = (props) => {
    return (
      <div className="input-container">
        <input 
          className="inputi"
          type={props.type}
          value={props.value}
          onChange={props.inputChenge}
          required
        />
        <span className="label">{props.childeren}</span>
    </div>
    );
  };
  export default Input