import React from "react";
import "./icons.css"
import { Link } from "react-router-dom";

const Icon=(props)=>{
    return(
        <div className="linktoicons" onClick={props.Clicked}>
          <div style={{backgroundImage: `url(${props.LOgo})` }} className="icon"></div>
          <div className="iconinfo" >{props.label}</div>
        </div>
    );
}
export default Icon
