import React, { useState } from "react";
import Input from '../component/input/input'
import SelectUser from "../component/selectuser/selectuser";
import './login.css'
import logo from "../assests/Mihan.png"
import { Link } from "react-router-dom";


const Login = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [selectValue, setselectValue] = useState('maneger');
  const options=[
  {key:1,value:'maneger',label:''},
  {key:2,value:'staf',label:''},
  {key:3,value:'companyans',label:''},
  {key:4,value:'hammiahan',label:''}];

  const handleSubmit = (e) => {
    e.preventDefault();
    if(email='rez@gmail.com'){}
  };

  const Input1=<Input
      type={"mail"}
      value={email}
      inputChenge={(e) => setEmail(e.target.value)}
      childeren={" "}
  /> 
  const Input2=<Input
       type={"password"}
       value={password}
       inputChenge={(e) => setPassword(e.target.value)}
       childeren={" "}
       options={options}
   />
   const Slct=<SelectUser
          value={selectValue}
          selectHandeler={(e)=>setselectValue(e.target.value)}
          options={options}
          ></SelectUser>
  return (
  <React.Fragment>
    <div className="toolbar">
      <div className="logo">
      <img src={logo} width="60" height="35" ></img>
      </div>
      <div className="noting">
      </div>
      <div className="homepage">  
      </div>
    </div>

    <div className="boxlg">
      <span className="text-centerlg">
        <h1  >
        <div className="boxlabellg"> </div>
        </h1>
        <form onSubmit={handleSubmit}>
          <div className="slctlg" >
          <span> </span>
            {Slct} 
            </div>
             {Input1} 
             {Input2}
          <Link  type="submit" className="btnlg"  to="./dash"></Link>
       </form>
       <div className="registerlg">
        <Link className="linktolg" to={'/register'}>   </Link>
        <Link className="linktolg" to={'/PasswordRecovery'}>  </Link>
       </div>
       
       </span>
    </div>
  </React.Fragment>
  );
};

export default Login;
